import React from 'react';
import { compose } from 'redux';
import { connect } from 'react-redux';
import { firestoreConnect } from 'react-redux-firebase';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import Spinner from './../layout/Spinner';

const Libros = (props) => {

    const { libros } = props;
    // console.log(libros);

    /* Validación de Libros */
    if(!libros) return <Spinner></Spinner>;

    // Eliminar Suscriptores
    const eliminarLibro = (id) => {
        console.log('Eliminando... ', id);
        // Eliminar
        props.firestore.delete({
            collection: 'libros',
            doc: id
        });
    }

    return(
        <div className="row">
            <div className="col-12 mb-4">
                <Link to="/libros/nuevo" className="btn btn-success">
                    <i className="fas fa-plus"></i> {''}
                    Nuevo Libro
                </Link>
            </div>
            <div className="col-md-8">
                <h2>
                    <i className="fas fa-book"></i> {''}
                    Libros
                </h2>
            </div>
            <table className="table table-striped mt-4">
                <thead className="text-light bg-primary">
                    <tr>
                        <th>Título</th>
                        <th>ISBN</th>
                        <th>Editorial</th>
                        <th>Existencia</th>
                        <th>Disponibles</th>
                        <th>Acciones</th>
                    </tr>
                </thead>
                <tbody>
                    {libros.map(libro => (
                        <tr key={libro.id}>
                            <td>{libro.titulo}</td>
                            <td>{libro.ISBN}</td>
                            <td>{libro.editorial}</td>
                            <td>{libro.existencia}</td>
                            <td>{libro.existencia - libro.prestados.length}</td>
                            <td>
                                <Link
                                        to={`/libros/mostrar/${libro.id}`}
                                        className="btn btn-success btn-block"
                                >
                                    <i className="fas fa-angle-double-right"></i> {''}
                                    Más Información
                                </Link>
                                <button 
                                        type="button"
                                        className="btn btn-danger btn-block"
                                        onClick={ () => eliminarLibro(libro.id) }
                                >
                                    <i className="fas fa-trash-alt"></i> {''}
                                    Eliminar
                                </button>
                            </td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    );
} 

Libros.propTypes = {
    firestore: PropTypes.object.isRequired
}

export default compose (
    firestoreConnect([{collection: 'libros'}]),
    connect((state, props) => ({
        // console.log(state)
        libros: state.firestore.ordered.libros
    }))
)(Libros); 
